.main-catalog {  
  padding-top: 25px;
  padding-bottom: 24px;

  border-top: 2px solid #e6e6e6;
  border-bottom: 2px solid #e6e6e6;

  .main-catalog__wrapper {
    width: 280px;
    padding: 0 20px;
  }

  h2 {    
    padding-bottom: 30px;

    border-bottom: 2px solid #e6e6e6;
  }

  ul {
    list-style: none;
  }
}

.main-catalog__item {
  padding-top: 22px;
  padding-bottom: 24px;
    
  border-bottom: 2px solid #e6e6e6;
  
  table {
    width: 140px;
    border-collapse: collapse;

    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
    line-height: 16px;
  }

  .main-catalog__buy {
    display: block;
    width: 280px;
    height: 40px;
    

    font-size: 16px;
    line-height: 20px;
    font-weight: 300;
    text-transform: uppercase;

    color: #ffffff;
    background-color: #68b738;
  }
}

table {
  td:last-child {
    text-align: right;
  }
}

.main-catalog__more {
  padding-top: 24px;
  padding-bottom: 24px;

  p {
    font-size: 12px;
    line-height: 16px;
  }
}

.main-catalog__more-show-all {
  display: block;
    width: 280px;
    height: 40px;
    

    font-size: 16px;
    line-height: 20px;
    font-weight: 300;
    text-transform: uppercase;

    color: #444444;
    background-color: #f2f2f2;
  }
